/* variables */
:root {
    --about-bg-color: #12130f;
    --about-text-color: rgba(224, 224, 224, 0.8);
    --about-icon-color: #df643a;
}

/* base */
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
    margin: 0;
    padding: 0;
}

a {
    font-style: normal;
    text-decoration: none;
}

button {
    cursor: pointer;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

body {
    font-family: 'Golos Text', sans-serif;
}

/* components */

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.list {
    padding: 0;
    margin: 0;
    list-style: none;
}

// .container {
//   padding: 0 20px;
//   margin: 0 auto;
// }

.title {
    font-family: 'Unbounded';
    font-weight: 500;
    font-size: 25px;
    line-height: 32px;

    letter-spacing: -0.01em;
    color: #ffffff;
}

.link {
    color: inherit;
    text-decoration: none;
}

.text {
    font-weight: 500;
    line-height: 26px;
    color: var(--about-text-color);
    max-width: 335px;
}

/* About section */

.about-section {
    border-radius: 24px;
    padding-top: 80px;
    padding-bottom: 80px;
    background-color: var(--about-bg-color);
}

.about-top-wrap,
.about-wrap {
    margin-bottom: 80px;
}

.about-top-list,
.about-list,
.about-bottom-list {
    margin-bottom: 40px;
}

.about-top-item:not(:last-child),
.about-item:not(:last-child),
.about-bottom-item:not(:last-child) {
    margin-bottom: 20px;
}

.about-top-subtitle {
    margin-bottom: 21px;
    line-height: 24px;

    color: #62d9f1;
}

.about-top-title,
.about-title,
.about-bottom-title {
    margin-bottom: 30px;
}

.about-icon {
    display: none;
}

/* Facts section */
.facts-section {
    padding: 100px 0;
}

.facts-container {
}

.container {
}

.facts-main-title {
    text-align: center;
    color: var(--about-bg-color);
    margin-bottom: 30px;
}

.facts-list-wrap {
    margin: 0 auto;
}

.facts-list {
    display: flex;

    flex-wrap: wrap;
    row-gap: 20px;
    justify-content: center;
    column-gap: 21px;
}

.facts-item {
    border: 1px solid var(--about-bg-color);
    border-radius: 24px;
    padding: 30px;
    width: 335px;
}

.facts-item:nth-child(3),
.facts-item:nth-child(4) {
    padding-bottom: 54px;
}

.facts-text {
    max-width: 275px;
}

.facts-icon-wrap {
    display: inline-flex;
    background-color: rgba(223, 100, 58, 0.1);
    border-radius: 50%;
    padding: 14px;
    align-items: center;
    justify-content: center;
    margin-bottom: 24px;
}

.facts-icon {
    width: 20px;
    height: 20px;
    fill: var(--about-icon-color);
}

.facts-title {
    font-size: 18px;
    line-height: 23px;
    font-family: 'Unbounded';
    font-weight: 500;
    margin-bottom: 12px;

    color: var(--about-bg-color);
}

.facts-text {
    display: inline-block;
    width: 348px;
    line-height: 24px;
    color: var(--about-bg-color);
}

/* Gallery section */

.gallery-section {
}

.gallery-container {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: center;
}

.gallery-list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.gallery-img-wrap {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.gallery-img {
    border-radius: 10px;
}

@media screen and (max-width: 767px) {
    // .container {
    //   max-width: 375px;
    // }
}

@media screen and (max-width: 768px) {
    // .container {
    //   padding: 0 60px;
    //   max-width: 1439px;
    // }
}

@media screen and (max-width: 1200px) {
    .about-top-list,
    .about-list,
    .about-bottom-list {
        text-align: center;
        margin-bottom: 70px;
    }

    .title {
        font-size: 32px;
    }
    .text {
        margin: 0 auto;
        font-size: 18px;
        max-width: 550px;
    }
    .about-top-img,
    .about-img,
    .about-bottom-img {
        margin: 0 auto;
    }

    .facts-main-title {
        margin-bottom: 60px;
    }
    .facts-list {
        display: block;
    }

    .facts-title {
        font-size: 20px;
    }
    .facts-item {
        margin: 0 auto;
    }
    .facts-item:not(:last-child) {
        margin-bottom: 20px;
    }
    .gallery-container,
    .gallery-img-wrap {
        gap: 30px;
    }
}

@media screen and (min-width: 1201px) {
    .title {
        font-size: 40px;
        line-height: 52px;

        letter-spacing: -0.02em;
    }
    .text {
        max-width: unset;
    }
    .about-top-wrap {
        display: flex;
        position: relative;
        justify-content: center;
        gap: 72px;
        margin-bottom: 44px;
    }

    .about-top-list {
        margin-top: 49px;
        display: flex;
        flex-direction: column;
        max-width: 538px;
    }

    .about-top-list,
    .about-list,
    .about-bottom-list {
        margin-bottom: 0px;
    }

    .about-top-item:not(:last-child) {
        margin-bottom: 20px;
    }
    .about-top-item:first-child {
        margin-bottom: 30px;
    }

    .about-top-title,
    .about-title,
    .about-bottom-title {
        margin-bottom: 0px;
    }

    .about-top-img-list {
        display: flex;
    }

    .about-top-img {
        border-radius: 24px;
    }
    .about-icon {
        width: 150px;
        height: 140px;
        display: block;
        fill: var(--about-icon-color);
        margin: 0 auto;
    }
    .about-icon.first {
        margin-bottom: 83px;
    }
    .about-icon.second {
        width: 100px;
        height: 88px;
        margin-bottom: 133px;
    }

    .about-wrap {
        display: flex;
        gap: 52px;
        margin-bottom: 107px;
        justify-content: center;
    }
    .about-list {
        margin-top: 49px;
        display: flex;
        flex-direction: column;
        max-width: 538px;
    }

    .about-item:not(:last-child) {
        margin-bottom: 20px;
    }
    .about-item:first-child {
        margin-bottom: 30px;
    }

    .about-img {
        border-radius: 24px;
    }

    .about-img.second {
        order: -1;
    }

    .about-bottom-wrap {
        display: flex;
        justify-content: center;
        gap: 93px;
    }
    .about-bottom-list {
        margin-top: 49px;
        max-width: 517px;
        display: flex;
        flex-direction: column;
    }
    .about-bottom-item:not(:last-child) {
        margin-bottom: 20px;
    }
    .about-bottom-item:first-child {
        margin-bottom: 30px;
    }

    .about-bottom-img-list {
        display: flex;
    }
    .about-bottom-img-item {
    }
    .about-bottom-img {
        border-radius: 24px;
    }

    .facts-main-title {
        margin-bottom: 50px;
    }
    .facts-section {
        padding: 220px 0;
    }

    .facts-item {
        border: 1px solid var(--about-bg-color);
        width: 428px;
    }

    .facts-item:nth-child(1),
    .facts-item:nth-child(2) {
        padding: 40px;
        padding-bottom: 51px;
    }

    .facts-item:nth-child(3),
    .facts-item:nth-child(4) {
        padding: 40px;
        padding-bottom: 75px;
    }

    .facts-item:nth-child(2) {
        margin-top: 40px;
    }
    .facts-item:nth-child(3) {
        margin-bottom: 40px;
    }

    .facts-text {
        max-width: 348px;
    }
    .gallery-container {
        gap: 20px;
    }

    .gallery-img {
        border-radius: 24px;
    }
    .gallery-img-wrap {
        gap: 20px;
    }
}

.about {
    &__hero {
        margin: 0 0 20px;
    }

    &__hero-content {
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;

        .text {
            font-weight: 400;
            font-size: 16px;
            line-height: 150%;
            /* identical to box height, or 24px */

            /* 800 */

            color: #49484d;
            margin-bottom: 50px;
        }
    }

    &__hero-preview {
        width: 100%;

        margin: 0 auto;
        overflow: hidden;

        img {
            height: 100%;
        }
    }
}