.order-page {
    position: relative;

    &__content {
        display: grid;
        grid-template-columns: 0.9fr 0.7fr;
        gap: 10%;
    }

    &__section {
        margin-top: 40px;
        padding-bottom: 42px;
        border-bottom: 1px solid #ebebee;
    }

    &__have-promocode {
        display: flex;
        align-items: center;
        gap: 12px;
        background: #62d9f1;
        width: fit-content;
        padding: 12px 24px;
        border-radius: 7px;
        margin: 24px 0 0 0;
    }

    &__arrow {
        line-height: 0;
        width: fit-content;
        height: fit-content;
        transform: rotate(180deg);

    }

    &__section-inputs {
        margin-top: 30px;
        display: flex;
        flex-direction: column;
        gap: 10px;

        &-row {
            flex-direction: row;
            align-items: flex-end;
            gap: 20px;
        }

        .inputNumber {
            max-width: 100%;
        }
    }

    &__block {
        width: 100%;

        span {
            display: block;
            font-weight: 400;
            font-size: 16px;
            line-height: 150%;
            /* identical to box height, or 24px */

            letter-spacing: -0.01em;

            /* Black */

            color: #12130f;
            padding-bottom: 16px;
        }
    }
}

.checkout {
    background: #12130f;
    height: fit-content;
    border-radius: 15px;

    &__content {
        padding: 40px;
    }

    &__row {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__list {
        margin-top: 24px;
        display: flex;
        flex-direction: column;
        gap: 12px;
    }

    &__text {
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;

        letter-spacing: -0.01em;

        color: #d9d9de;

        &-bold {
            font-weight: 500;
            font-size: 18px;
            line-height: 150%;
            /* identical to box height, or 27px */

            text-align: center;
            letter-spacing: -0.01em;

            color: #ffffff;
        }
    }

    &__item {
        background: rgba(255, 255, 255, 0.1);
        border-radius: 10px;
        padding: 12px;

        &-name {
            font-weight: 400;
            font-size: 16px;
            line-height: 150%;

            color: #ffffff;
            letter-spacing: -0.01em;
        }

        &-count {
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 150%;

            letter-spacing: -0.01em;

            color: #bdbdbd;
        }

        &-weight {
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            line-height: 150%;

            letter-spacing: -0.01em;

            color: #b4b4bc;
        }

        &-price {
            font-weight: 500;
            font-size: 16px;
            line-height: 150%;

            text-align: right;
            letter-spacing: -0.01em;

            color: #ffffff;
        }
    }

    &__total {
        margin-top: 126px;
        display: flex;
        flex-direction: column;
        gap: 12px;
    }
}

@media screen and (max-width: 1366px) {
    .order-page {
        &__content {
            grid-template-columns: 1fr 0.9fr;
        }
    }
}

@media screen and (max-width: 1024px) {
    .order-page {
        &__content {
            margin: 0 auto;
            max-width: 700px;
            grid-template-columns: 90%;
