.inputTextArea {
  display: flex;
  flex-direction: column;
  gap: 7px;
  width: 100%;

  span {
    font-weight: 400;
    font-size: 12px;

    color: #757479;
  }
  textarea {
    min-height: 96px;
    background: #f3f3f5;
    border-radius: 5px;
    border: none;
    outline: none;
    padding: 13px 14px;
  }
  input,
  select {
    background: #f3f3f5;
    border-radius: 5px;
    width: 100%;
    padding: 14px 12px;
  }
}
